// Import three.js using the correct path for Vite
import * as THREE from '/node_modules/three/build/three.module.js';
import { GLTFLoader } from '/node_modules/three/examples/jsm/loaders/GLTFLoader.js';

// Original Three.js setup code remains the same until renderer setup
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
const renderer = new THREE.WebGLRenderer({ alpha: false }); // Disable transparency
renderer.setClearColor(0x000000, 1); // Set the clear color to black
renderer.setSize(window.innerWidth, window.innerHeight);
document.body.appendChild(renderer.domElement);

// Add CSS to the document
const style = document.createElement('style');
style.textContent = `
.social-media-container {
    position: fixed;
    bottom: 20px;
    right: 20px;
    display: flex;
    gap: 15px;
    z-index: 1000;
    opacity: 0;
    transform: translateX(100px);
    transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.social-media-container.visible {
    opacity: 1;
    transform: translateX(0);
}

.social-icon {
    width: 40px;
    height: 40px;
    background: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    animation: popIn 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) backwards;
}

@keyframes popIn {
    0% {
        opacity: 0;
        transform: scale(0) translateY(20px);
    }
    100% {
        opacity: 1;
        transform: scale(1) translateY(0);
    }
}

.social-icon:nth-child(1) { animation-delay: 0.1s; }
.social-icon:nth-child(2) { animation-delay: 0.2s; }
.social-icon:nth-child(3) { animation-delay: 0.3s; }

.social-icon:hover {
    transform: translateY(-5px) scale(1.1);
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2);
}

.social-icon.facebook:hover {
    background: #1877f2;
}

.social-icon.twitter:hover {
    background: #000000;
}

.social-icon.instagram:hover {
    background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
}

.social-icon svg {
    width: 24px;
    height: 24px;
    transition: all 0.3s ease;
}

.social-icon:hover svg {
    fill: white;
}
`;
document.head.appendChild(style);

// Create social media icons container
const socialContainer = document.createElement('div');
socialContainer.className = 'social-media-container';

// Social media icons data
const socialIcons = [
    {
        name: 'facebook',
        url: 'https://facebook.com',
        svg: `<svg fill="currentColor" viewBox="0 0 24 24"><path d="M14 13.5h2.5l1-4H14v-2c0-1.03 0-2 2-2h1.5V2.14c-.326-.043-1.557-.14-2.857-.14C11.928 2 10 3.657 10 6.7v2.8H7v4h3V22h4v-8.5z"/></svg>`
    },
    {
        name: 'twitter',
        url: 'https://x.com',
        svg: `<svg fill="currentColor" viewBox="0 0 24 24"><path d="M18.244 2.25h3.308l-7.227 8.26 8.502 11.24H16.17l-5.214-6.817L4.99 21.75H1.68l7.73-8.835L1.254 2.25H8.08l4.713 6.231zm-1.161 17.52h1.833L7.084 4.126H5.117z"/></svg>`
    },
    {
        name: 'instagram',
        url: 'https://instagram.com',
        svg: `<svg fill="currentColor" viewBox="0 0 24 24"><path d="M12 2.163c3.204 0 3.584.012 4.85.07 3.252.148 4.771 1.691 4.919 4.919.058 1.265.069 1.645.069 4.849 0 3.205-.012 3.584-.069 4.849-.149 3.225-1.664 4.771-4.919 4.919-1.266.058-1.644.07-4.85.07-3.204 0-3.584-.012-4.849-.07-3.26-.149-4.771-1.699-4.919-4.92-.058-1.265-.07-1.644-.07-4.849 0-3.204.013-3.583.07-4.849.149-3.227 1.664-4.771 4.919-4.919 1.266-.057 1.645-.069 4.849-.069zM12 0C8.741 0 8.333.014 7.053.072 2.695.272.273 2.69.073 7.052.014 8.333 0 8.741 0 12c0 3.259.014 3.668.072 4.948.2 4.358 2.618 6.78 6.98 6.98C8.333 23.986 8.741 24 12 24c3.259 0 3.668-.014 4.948-.072 4.354-.2 6.782-2.618 6.979-6.98.059-1.28.073-1.689.073-4.948 0-3.259-.014-3.667-.072-4.947-.196-4.354-2.617-6.78-6.979-6.98C15.668.014 15.259 0 12 0zm0 5.838a6.162 6.162 0 100 12.324 6.162 6.162 0 000-12.324zM12 16a4 4 0 110-8 4 4 0 010 8zm6.406-11.845a1.44 1.44 0 100 2.881 1.44 1.44 0 000-2.881z"/></svg>`
    }
];

// Create and append social icons
socialIcons.forEach(icon => {
    const iconElement = document.createElement('a');
    iconElement.href = icon.url;
    iconElement.target = '_blank';
    iconElement.className = `social-icon ${icon.name}`;
    iconElement.innerHTML = icon.svg;
    socialContainer.appendChild(iconElement);
});

// Don't append the container immediately
// We'll append it after the spinning stops

// Rest of your Three.js code
const light = new THREE.AmbientLight(0xffffff, 3);
scene.add(light);

// Your existing variables
let modelRotation = {
    x: 0,
    y: 0
};
let spinSpeed = 0.1;
let isSpinning = true;
let isDragging = false;
let previousMousePosition = { x: 0, y: 0 };

// Zoom animation variables
let currentScale = 0;
const targetScale = 3.0;
const scaleSpeed = 0.04;
let isZooming = true;

// Set fixed camera position
camera.position.set(0, 4, 5);
camera.lookAt(0, 2, 0);

// Create a container for the model
const modelContainer = new THREE.Group();
scene.add(modelContainer);

// Load the GLTF model
const loader = new GLTFLoader();
loader.load('/rose/scene.gltf', (gltf) => {
    modelContainer.add(gltf.scene);
    modelContainer.scale.set(currentScale, currentScale, currentScale);
}, undefined, (error) => {
    console.error('An error occurred while loading the model:', error);
});

// Mouse event listeners
window.addEventListener('mousedown', (event) => {
    if (event.button === 0) {
        isDragging = true;
        isSpinning = false;
    }
});

window.addEventListener('mousemove', (event) => {
    if (isDragging) {
        const deltaX = event.clientX - previousMousePosition.x;
        const deltaY = event.clientY - previousMousePosition.y;
        modelRotation.y += deltaX * 0.01;
        modelRotation.x += deltaY * 0.01;
        modelRotation.x = Math.max(-Math.PI / 2, Math.min(Math.PI / 2, modelRotation.x));
    }
    previousMousePosition = { x: event.clientX, y: event.clientY };
});

window.addEventListener('mouseup', (event) => {
    if (event.button === 0) {
        isDragging = false;
    }
});

// Track if social icons have been shown
let socialIconsShown = false;

// Animation loop
function animate() {
    requestAnimationFrame(animate);
    
    // Handle zoom animation
    if (isZooming && currentScale < targetScale) {
        currentScale += scaleSpeed;
        if (currentScale >= targetScale) {
            currentScale = targetScale;
            isZooming = false;
        }
        modelContainer.scale.set(currentScale, currentScale, currentScale);
    }
    
    // Handle spinning animation
    if (isSpinning) {
        modelRotation.y += spinSpeed;
        spinSpeed *= 0.99;
        if (spinSpeed < 0.01) {
            isSpinning = false;
            spinSpeed = 0;
            
            // Show social icons when spinning stops
            if (!socialIconsShown) {
                document.body.appendChild(socialContainer);
                // Use setTimeout to ensure the transition happens
                setTimeout(() => {
                    socialContainer.classList.add('visible');
                }, 100);
                socialIconsShown = true;
            }
        }
    }

    // Apply rotation to the model container
    modelContainer.rotation.x = modelRotation.x;
    modelContainer.rotation.y = modelRotation.y;

    renderer.render(scene, camera);
}

// Start the animation
animate();

// Handle window resize
window.addEventListener('resize', () => {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
});

